import React from 'react'
import '../styles/Experience.css'
import { BiGroup } from 'react-icons/bi'
import { BsStars } from 'react-icons/bs'
import { IoTrophyOutline } from 'react-icons/io5'
import { RiMacbookLine } from 'react-icons/ri'

function Experience() {
    return (
        <section className="experience">
            <div className="max-width">
                <div className="cards">
                    <div className="card">
                        <div className="icon">
                            <BiGroup />
                        </div>
                        <div className="number">
                            596+
                        </div>
                        <div className="text">
                            Parents Reviews
                        </div>
                    </div>
                    <div className="card">
                        <div className="icon">
                            <BsStars />
                        </div>
                        <div className="number">
                            30K
                        </div>
                        <div className="text">
                            Good Students
                        </div>
                    </div>
                    <div className="card">
                        <div className="icon">
                            <IoTrophyOutline />
                        </div>
                        <div className="number">
                            758+
                        </div>
                        <div className="text">
                            Program Awards
                        </div>
                    </div>
                    <div className="card">
                        <div className="icon">
                            <RiMacbookLine />
                        </div>
                        <div className="number">
                            986+
                        </div>
                        <div className="text">
                            Digital Laboratory
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Experience
