import React from 'react'
import '../styles/Home.css'

function Home() {

    function ScrollToAbout() {
        var element = document.querySelector("#about");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function ScrollToReg() {
        var element = document.querySelector("#registration");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    return (
        <section className="home" id="home">
            <div className="max-width">
                <div className="container">
                    <div className="title1">
                        Education make bright your future
                    </div>
                    <div className="title2">
                        Best Experience Learning <br /> University For You
                    </div>
                    <div className="title3">
                        Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nesciunt, deserunt?
                    </div>
                    <div className="btns">
                        <div className="primary-btn" onClick={ScrollToAbout}>
                            <button>About Us</button> 
                        </div>
                        <div className="second-btn" onClick={ScrollToReg}>
                            <button>Registration</button> 
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Home
