import React, { useState } from 'react'
import './styles/App.css';
import Home from './components/Home';
import Navbar from './components/Navbar';
import About from './components/About';
import About2 from './components/About2';
import Events from './components/Events';
import Registration from './components/Registration';
import Experience from './components/Experience';
import ChooseUs from './components/ChooseUs';
import Partners from './components/Partners';
import Footer from './components/Footer';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCaretUp } from '@fortawesome/free-solid-svg-icons'

function App() {

  const [scrollUp, setScrollUpBtn] = useState(false);

  const changeBaackgroundNavbar = () => {
      if (window.scrollY >= 50) {
        setScrollUpBtn(true)
      } else {
        setScrollUpBtn(false)
      }
  }

window.addEventListener('scroll', changeBaackgroundNavbar);

  function ScrollToHome() {
    var element = document.querySelector("#home");
    element.scrollIntoView({ behavior: 'smooth', block: 'end'});
  }

  return (
    <div className="App">
      <div class= {scrollUp ? 'scroll-up-btn fixed' : 'scroll-up-btn'} onClick={ScrollToHome}>
          <div class="btn-container">
            <FontAwesomeIcon icon={faCaretUp} />
          </div>
      </div>
      <Navbar/>
      <Home/>
      <About/>
      <About2/>
      <Events/>
      <Registration/>
      <Experience/>
      <ChooseUs/>
      <Partners/>
      <Footer/>
    </div>
  );
}

export default App;
