import React from 'react'
import '../styles/Events.css'
import { BiTime } from 'react-icons/bi'
import { MdDateRange } from 'react-icons/md'
import { BsArrowRightShort } from 'react-icons/bs'

function Events() {
    return (
        <section className="events" id="events">
            <div className="max-width">
                <div className="title">
                    Our Upcoming <br /> Events
                </div>
                <div className="container">
                    <div className="card">
                        <div className="overlay">
                            <div className="date-time">
                                <div className="date">
                                    <MdDateRange />
                                    25 Aug 2019
                                </div>
                                <div className="time">
                                    <BiTime />
                                    10:00 am - 01:00 pm
                                </div>
                            </div>
                            <div className="card-title">
                                How To Improve Your Computer Skills
                                <div className="icon-container">
                                    <BsArrowRightShort/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="card">
                        <div className="overlay">
                            <div className="date-time">
                                <div className="date">
                                    <MdDateRange />
                                    25 Aug 2019
                                </div>
                                <div className="time">
                                    <BiTime />
                                    10:00 am - 01:00 pm
                                </div>
                            </div>
                            <div className="card-title">
                                How To Build An Endless Runner Game Virtual
                                <div className="icon-container">
                                    <BsArrowRightShort/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="card">
                        <div className="overlay">
                            <div className="date-time">
                                <div className="date">
                                    <MdDateRange />
                                    25 Aug 2019
                                </div>
                                <div className="time">
                                    <BiTime />
                                    10:00 am - 01:00 pm
                                </div>
                            </div>
                            <div className="card-title">
                                UX/UI Design Meetup Version 01 In 2019
                                <div className="icon-container">
                                    <BsArrowRightShort/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Events
