import React from 'react'
import '../styles/Footer.css'
import logo from '../assets/images/footer-logo.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF, faTwitter, faInstagram, faWhatsapp } from '@fortawesome/free-brands-svg-icons'

function Footer() {
    return (
        <footer>
            <div className="max-width">
                <div className="logo-container">
                    <img src={logo} alt="" />
                </div>
                <div className="text-container">
                    <div className="text">
                        Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloque laudantium totam rem aperiam, eaque ipsa quae.
                    </div>
                </div>
                <div className="devider"></div>
                <div className="bottom-footer">
                    <div className="copy">
                        Copyright 2021. All rights reserved
                    </div>
                    <div className="social-icons">
                        <FontAwesomeIcon icon={faFacebookF} />
                        <FontAwesomeIcon icon={faInstagram} />
                        <FontAwesomeIcon icon={faTwitter} />
                        <FontAwesomeIcon icon={faWhatsapp} />
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer
