import React from 'react'
import '../styles/Partners.css'
import OwlCarousel from 'react-owl-carousel2'
import 'react-owl-carousel2/lib/styles.css'
import 'react-owl-carousel2/src/owl.theme.default.css'

function Partners() {

    const options = {
        items: 6,
        nav: false,
        dots: false,
        rewind: true,
        autoplay: true,
        loop:true,
        autoplayTimeout:2000,
        responsive:{
            0:{
                items:2
            },
            600:{
                items:3
            },
            1000:{
                items:6
            }
        }
    };

    return (
        <section className="partners">
            <div className="max-width">
                <div className="title">
                    Our partners
                </div>
                <div className="container">
                    <OwlCarousel options={options}>
                        <div>
                            <div className="client img1"></div>
                        </div>
                        <div>
                            <div className="client img2"></div>
                        </div>
                        <div>
                            <div className="client img3"></div>
                        </div>
                        <div>
                            <div className="client img4"></div>
                        </div>
                        <div>
                            <div className="client img5"></div>
                        </div>
                        <div>
                            <div className="client img6"></div>
                        </div>
                        <div>
                            <div className="client img3"></div>
                        </div>
                        <div>
                            <div className="client img4"></div>
                        </div>
                    </OwlCarousel>
                </div>
            </div>
        </section>
    )
}

export default Partners
