import React from 'react'
import '../styles/Navbar.css'
import logo from '../assets/images/logo.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons'

function Navbar() {

    function ScrollToHome() {
        var element = document.querySelector("#home");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function ScrollToAbout() {
        var element = document.querySelector("#about");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function ScrollToFormations() {
        var element = document.querySelector("#formations");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function ScrollToEvents() {
        var element = document.querySelector("#events");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function ScrollToReg() {
        var element = document.querySelector("#registration");
        element.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }

    function MenuClick(){
        var element = document.getElementById("menu");
        element.classList.toggle("active");
        var element2 = document.getElementById("menu-icon");
        element2.classList.toggle("active");
    }

    return (
        <nav>
            <div className="max-width">
                <div className="container">
                    <div className="logo" onClick={ScrollToHome}>
                        <img src={logo} alt="" />
                    </div>
                    <ul className="menu" id="menu">
                        <li onClick={ScrollToHome}>Home</li>
                        <li onClick={ScrollToAbout}>About Us</li>
                        <li onClick={ScrollToFormations}>Formations</li>
                        <li onClick={ScrollToEvents}>Events </li>
                        <li onClick={ScrollToReg}>Registration</li>
                    </ul>
                    <div className="menu-icon" id="menu-icon" onClick={MenuClick}>
                        <FontAwesomeIcon icon={faBars} />
                        <FontAwesomeIcon icon={faTimes} />
                    </div>
                </div>
            </div>
        </nav>
    )
}

export default Navbar
